import Base from './Base'

class User extends Base {

    /*
    *
    */
    login(email,password){
        return new Promise(async (resolve,reject) => {
            try {
                let result = await this.request("POST","/user/login",{"email":email,"password":password});
                resolve(result.data);
            } catch (error) {
                reject(error)
            }
        })

    }

    /*
    *
    */
    register(email,name,password){
        return new Promise(async (resolve,reject) => {
            try {
                let result = await this.request("POST","/user/register",{"email":email,"name":name,"password":password});
                resolve(result)
            } 
            catch (error) {
                reject(error)
            }
        })
    }

    /*
    * 
    */
    getUserInfo(){
        return new Promise(async (resolve,reject) => {
            try {
                let result = await this.request("GET","/user/profile",null,{
                    'needAuth':true
                });
                resolve(result)
            } 
            catch (error) {
                reject(error)
            }
        })        
    }

    /*
    *
    */
    select(user_id){
        return new Promise(async (resolve,reject) => {
            try {
                let result = await this.request("GET","user/select",{'user_id':user_id},{
                    'needAuth':true
                });
                resolve(result)
            } 
            catch (error) {
                reject(error)
            }
        })  
    }

    /*
    * 
    */
    resetpassword(password){
        return new Promise(async (resolve,reject) => {
            try {
                let result = await this.request("POST","/user/password/reset",{'newpassword':password},{
                    'needAuth':true
                });
                resolve(result)
            } 
            catch (error) {
                reject(error)
            }
        })   
    }

    UserList(){
        return new Promise(async (resolve,reject) => {
            try {
                let result = await this.request("GET","/user/UserList",null,{
                    'needAuth':true
                });
                resolve(result)
            } 
            catch (error) {
                reject(error)
            }
        })     
    }

    DeleteUser(user_id){
        return new Promise(async (resolve,reject) => {
            try {
                let result = await this.request("POST","/user/DeleteUser",{"user_id":user_id},{
                    'needAuth':true
                });
                resolve(result)
            } 
            catch (error) {
                reject(error)
            }
        }) 
    }

    UpdateAdmin(user_id){
        return new Promise(async (resolve,reject) => {
            try {
                let result = await this.request("POST","/user/UpdateAdmin",{"user_id":user_id},{
                    'needAuth':true
                });
                resolve(result)
            } 
            catch (error) {
                reject(error)
            }
        }) 
    }

    degrade(user_id){
        return new Promise(async (resolve,reject) => {
            try {
                let result = await this.request("POST","/user/degrade",{"user_id":user_id},{
                    'needAuth':true
                });
                resolve(result)
            } 
            catch (error) {
                reject(error)
            }
        }) 
    }
}

export default new User();


