export default {
    path: 'ctf',
    component: resolve => require(['@/components/ctf/layout.vue'], resolve),
    redirect: '/ctf/list',
    children:[
    {
      path: 'list',
      name: 'ChallengeList',
      component: resolve => require(['@/components/ctf/list.vue'], resolve),
    },
]
}



